var animationend = ['webkitAnimationEnd', 'mozAnimationEnd', 'MSAnimationEnd', 'oanimationend', 'animationend'];
var animationCSS = document.getElementsByClassName("app-icon");

setInterval(function() {
  for (let i = 0; i < animationCSS.length; i++) {
    if (animationCSS[i].alt == 'Right icon 2') {
      animationCSS[i].classList.add('wave');
      animationend.forEach( evt => {
        animationCSS[i].addEventListener(evt, function() {
          // console.log(animationCSS[i]);
          animationCSS[i].classList.remove('wave');
        }, false);
      });
    }
  }
}, 3000)