<template>
  <tag :class="options + ' ' + color">
    <img class="icon" :src="getImgUrl.imgURL" :style="getImgUrl.imgStyles">{{ title }}
  </tag>
</template>

<script>
  export default {
    name: 'Card',
    props: {
      title: String,
      icon: String,
      color: String,
      options: String,
    },
    data() {
      return {
        'TagColor': this.options + ' ' + this.color,
        'ImgURL': require('@/assets/images/tag/' + this.icon),
      }
    },
    computed: {
      getImgUrl() {
        if (!this.ImgURL) {
          // console.log(this.ImgURL);
          // console.log("不存在");
          return { 
            'imgURL': this.img,
            'imgStyles': 'display: none',
          }
        } else { 
          // console.log(this.ImgURL);
          // console.log("存在");
          return {
            'imgURL': this.ImgURL,
          }
        }
      }
    },
  }
</script>

